#songs-list {
    padding: 0;
    list-style-type: none;
    margin: 0 0 20px;
}

.song-wrapper {
	height: 80px;
	position: relative;
}

.song-container {
	position: relative;
	height: 100%;
    display: -ms-flexbox;
    display: flex;
    background-color: #322427;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -webkit-box-shadow: 0 -1px 0 #231B1D inset;
            box-shadow: 0 -1px 0 #231B1D inset;
    padding: 0 0 0 5%;
    z-index: 2;
    -webkit-transition: -webkit-transform 0.3s;
    transition: -webkit-transform 0.3s;
    -o-transition: transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
}

.song-wrapper:hover .song-container {
	background-color: #231B1D;
	-webkit-box-shadow: 5px 0 0 #DB1D40 inset;
	        box-shadow: 5px 0 0 #DB1D40 inset;
}

.song-container.dload-link-active {
	-webkit-transform: translateX(-100px);
	    -ms-transform: translateX(-100px);
	        transform: translateX(-100px);
}

.song-thumbnail {
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background-size: cover;
    margin-right: 5%;
}

.song-info-container {
    -ms-flex: 5 1 0%;
        flex: 5 1 0%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: center;
        justify-content: center;
}

.download-icon-container {
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
    max-width: 80px;
    min-width: 30px;
    color: #74676A;
}

.song-wrapper:hover .download-icon-container{
	color: #DB1D40;
}

.song-title {
    color: white;
    font-size: 0.8em;
    margin: 10px 0 5px 0;
}

.song-channel-name {
    font-size: 0.7em;
    margin: 0;
    text-transform: uppercase;
    color: #74676A;
}

.song-dload-links {
	height: 100%;
	width: 100px;
	position: absolute;
	top: 0;
	right: 0;
	z-index: 1;
}

.song-dload-links a {
	height: 40px;
	display: -ms-flexbox;
	display: flex;
	text-transform: uppercase;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	color: white;
	text-decoration: none;
	font-size: 0.7em;
}

.song-dload-links:hover + .song-container {
	-webkit-transform: translateX(-100px) !important;
	    -ms-transform: translateX(-100px) !important;
	        transform: translateX(-100px) !important;
}

.audio-dload-link {
	background-color: #74676A;
}

.video-dload-link {
	background-color: #685B5E;
}


